
.btnContainer {
  text-align: center;
  margin-top: 50px;

  .btnStyle {
    background-color:#00bcd4;
    padding: 0px 40px;
    color: white;
  }
}

.paymentWrap {
  text-align:center;

  .header {
    font-Size:15px;
    font-weight:700;
  }

  .subHeader {
    font-size:12px;
    font-weight:600;
  }

  .imageRow {
    margin-top: 30px;

    .selected {
      border: 1px solid blue;
    }

    .fontDecor {
      font-size:12px;
      font-weight:600;
    }

  }

  .contactRow {
    margin-top:40px;

    .fontDecor {
      font-size:12px;
      font-weight:600;
    }

    .linkDecor {
      font-size:12px;
      font-weight:600;
      color:#00bcd4
    }
  }
}

.inputRow {
  padding: 0 50px;
  text-align: center;
}

.inputBlock {
  display: block;
}

.paymentResult {
  font-size: 64px;
}

.paymentResultFailed {
  font-size: 64px;
  color:#ff9933;
  margin-bottom:15px;
}

.paymentResultSuccess {
  font-size: 64px;
  color:#13da76;
  margin-bottom:15px;
}